import { Block, BlockChildren, Data } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

interface OnlineVideoData extends Data {
  readonly src?: string;
  readonly type?: string;
  readonly poster?: string;
}

type OnlineVideoChildren = BlockChildren;

export interface OnlineVideoProperties<D = OnlineVideoData, N extends BlockChildren = OnlineVideoChildren> {
  data?: D;
  nodes?: N;
}

export default class OnlineVideo extends Block<OnlineVideoData, OnlineVideoChildren> {
  static TYPE = ELEMENT_TYPE;

  static create<SD = OnlineVideoData, SN extends BlockChildren = OnlineVideoChildren>(
    properties: OnlineVideo | OnlineVideoProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`OnlineVideo.create` does not accept string');
    }
    if (OnlineVideo.isOnlineVideo(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: OnlineVideo.TYPE,
    });
  }

  static isOnlineVideo(maybeOnlineVideo: any): maybeOnlineVideo is OnlineVideo {
    return Block.isBlock(maybeOnlineVideo) && maybeOnlineVideo.type === OnlineVideo.TYPE;
  }
}
